// фонове зображення
.hero-homepage {
  background-image: linear-gradient($hero-background, $hero-background),
    url(../images/hero/homepage/hero-homepage-mobile.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-color: $hero-background;

  @media screen and (min-device-pixel-ratio: 2) and (max-width: 414px),
    screen and (min-resolution: 192dpi) and (max-width: 414px),
    screen and (min-resolution: 2dppx) and (max-width: 414px) {
    background-image: linear-gradient($hero-background, $hero-background),
      url(../images/hero/homepage/hero-homepage-mobile@2x.jpg);
  }

  @media screen and (min-width: 1024px) {
    background-image: linear-gradient($hero-background, $hero-background),
      url(../images/hero/homepage/hero-homepage-tablet.jpg);
  }

  @media screen and (min-device-pixel-ratio: 2) and (min-width: 1024px),
    screen and (min-resolution: 192dpi) and (min-width: 1024px),
    screen and (min-resolution: 2dppx) and (min-width: 1024px) {
    background-image: linear-gradient($hero-background, $hero-background),
      url(../images/hero/homepage/hero-homepage-tablet@2x.jpg);
  }

  @media screen and (min-width: 1280px) {
    background-image: linear-gradient($hero-background, $hero-background),
      url(../images/hero/homepage/hero-homepage-desktop.jpg);
  }

  @media screen and (min-device-pixel-ratio: 2) and (min-width: 1280px),
    screen and (min-resolution: 192dpi) and (min-width: 1280px),
    screen and (min-resolution: 2dppx) and (min-width: 1280px) {
    background-image: linear-gradient($hero-background, $hero-background),
      url(../images/hero/homepage/hero-homepage-desktop@2x.jpg);
  }
}
